.App {
  display: grid;
  grid-column-gap: 1rem;
  grid-template-columns: 1.25fr 5fr;
}

li {
  list-style-type: none;
}

.charSection {
  grid-column: 1;
  padding: 1px;
  margin: 0%;
}

.weapMint {
  grid-column: 3;
}
.weapMint button {
  height: 75%;
}

.weapImg {
  margin-top: -15%;
  grid-column: 1;
  margin-left: 15%;
  position: relative;
  transform: rotate(45deg);
}
.weapImgInfo {
  grid-column: 2;
  padding-left: 25%;
}

.weapSection {
  grid-column: 2;
  padding: 0px;
  margin: 0%;
}

.weapInfo {
  grid-column: 1;
}

.weapDesc {
  grid-column: 2;

}

.weap {

  display: grid;
  grid-column-gap: 0.5fr;
  grid-template-columns: 40% 40% 20%;
  padding: 15px;
  padding-bottom: 5%;
  width: 50%;
  height: 75px;
  border: 1px black solid;
  margin: 1%;
}

.weapShow {
  /*background-image: url('images/PlaceSword.jpg');
  background-size: cover;*/
  display: grid;
  grid-column-gap: 0.5fr;
  grid-template-columns: 40% 40% 20%;
  padding: 15px;
  padding-bottom: 5%;
  width: 50%;
  height: 75px;
  border: 1px black solid;
  margin: 1%;
}

.weapShow:hover {
  background: #E3EFF0;
}

.weap:hover {
  background: #E3EFF0;
}
